

import Image from 'next/image';
import React from 'react';

const TimelineEvent = ({ year, content, isLeft }) => {
  return (
    <div className="relative">
      <div className="flex items-center justify-center">
        <div className="bg-green-700 text-white px-6 py-2 rounded-full">
          {year}
        </div>
      </div>
      <div className={`mt-4 ${isLeft ? 'mr-auto pr-8' : 'ml-auto pl-8'} 
        bg-gray-200 p-6 rounded-lg max-w-md`}>
        {content}
      </div>
    </div>
  );
};

const AboutPage = () => {
  const timelineEvents = [
    { year: '2014', content: "After Vishal Garg's first attempt to purchase his own dream home..." },
    { year: '2015', content: "Better Mortgage funds its first mortgage loan entirely online..." },
    { year: '2016', content: "Better Mortgage becomes a Fannie Mae approved seller + servicer..." },
    { year: '2017', content: "Better expands into the real estate market with Better Real Estate." },
    { year: '2018', content: "Better Mortgage partners with Ally Bank to build Ally powered by Better." },
    { year: '2019', content: "Better Mortgage launches its pilot partnership with American Express..." }
  ];

  return (
    <div className="max-w-7xl mx-auto px-4">
      <div className="py-20 text-center">
        <h2 className="text-green-700 font-medium mb-6">Our mission</h2>
        <h1 className="text-4xl md:text-5xl font-medium max-w-4xl mx-auto leading-tight">
          We're making homeownership simpler, faster â€” and most importantly, more accessible for all Americans.
        </h1>
      </div>

      <div className="grid md:grid-cols-2 gap-8 items-center mb-20">
        <div>
          <h2 className="text-3xl font-medium mb-6">The status quo is broken</h2>
          <p className="text-gray-600 mb-6">
            The traditional processes around homeownership are opaque and stressful...
          </p>
          <button className="bg-green-700 text-white px-6 py-3 rounded-md hover:bg-green-800 transition-colors">
            Read Vishal's story
          </button>
        </div>
        <div className="relative">
          <div className="aspect-w-16 aspect-h-9 bg-gray-200 rounded-lg">
            <Image
              src="/api/placeholder/600/400"
              alt="CEO Video Placeholder"
              width={600}
              height={400}
              className="rounded-lg object-cover"
            />
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center">
                <div className="w-0 h-0 border-t-8 border-b-8 border-l-12 border-transparent border-l-gray-800 ml-1"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="bg-green-700 text-white py-20 px-8 -mx-4">
        <div className="max-w-4xl mx-auto">
          <h2 className="text-3xl font-medium mb-8">How we're changing things</h2>
          <p className="mb-6">Homeownership is a huge part of our economy...</p>
        </div>
      </div>

      <div className="py-20">
        <h2 className="text-3xl font-medium text-center mb-12">Backed by</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-8 items-center max-w-4xl mx-auto">
          {["SoftBank", "Ally", "Citi", "Ping An Bank", "Goldman Sachs", "KPCB", "American Express"].map((name, index) => (
            <Image
              key={index}
              src={`/api/placeholder/120/60`}
              alt={name}
              width={120}
              height={60}
              className="h-8 object-contain"
            />
          ))}
        </div>
      </div>

      <div className="py-20">
        <h2 className="text-3xl font-medium text-center mb-12">Company timeline</h2>
        <div className="relative">
          <div className="absolute left-1/2 transform -translate-x-1/2 h-full w-px bg-gray-300"></div>
          <div className="space-y-16">
            {timelineEvents.map((event, index) => (
              <TimelineEvent key={event.year} year={event.year} content={event.content} isLeft={index % 2 === 0} />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutPage;